import { useState } from "react";
import { Link } from "react-router-dom";
import {
  Formik,
  Form,
  Field,
  ErrorMessage as FormikErrorMessage,
} from "formik";
import * as Yup from "yup";
import ErrorMessage from "../errorMessage/ErrorMessage";
import useMarvelServices from "../../services/MarvelService";

import "./charSearchForm.scss";

const CharSearchForm = () => {
  const [char, setChar] = useState(null);

  const { loading, error, getCharacterByName, clearError } =
    useMarvelServices();

  const updateChar = (name) => {
    clearError();
    getCharacterByName(name).then(onCharLoaded);
  };

  const onCharLoaded = (char) => {
    setChar(char);
    console.log(char);
  };

  const errorMessage = error ? (
    <div className="char__search-critical-error">
      <ErrorMessage />
    </div>
  ) : null;

  const results = !char ? null : char.length > 0 ? (
    <div className="char__search-wrapper">
      <div className="char__search-success">
        There is! Visit {char[0].name} page?
      </div>
      <Link
        to={`/characters/${char[0].id}`}
        className="button button__secondary"
      >
        <div className="inner">To page</div>
      </Link>
    </div>
  ) : (
    <div className="char__search-error">
      The character was not found. Check the name and try again
    </div>
  );

  return (
    <div className="char__search-form">
      <Formik
        initialValues={{
          charSearch: "",
        }}
        validationSchema={Yup.object({
          charSearch: Yup.string().required("This field is required"),
        })}
        onSubmit={({ charSearch }) => updateChar(charSearch)}
      >
        <>
          <Form>
            <label className="char__search-label" htmlFor="charSearch">
              Or find a character by name:
            </label>
            <div className="char__search-wrapper">
              <Field
                id="charSearch"
                name="charSearch"
                type="text"
                placeholder="Enter name"
              />
              <button
                type="submit"
                className="button button__main"
                disabled={loading}
              >
                <div className="inner">find</div>
              </button>
            </div>
            <FormikErrorMessage name="charSearch" className="char__search-error" component="div" />
          </Form>
          {results}
          {errorMessage}
        </>
      </Formik>
    </div>
  );
};

export default CharSearchForm;
